import { css } from '@emotion/react';
import Head from 'next/head';
import { useRouter } from 'next/router';

import { Typography } from 'src/components/dataDisplay';
import { Button } from 'src/components/inputs';
import { Stack } from 'src/components/layout';
import { Paper } from 'src/components/surface';
import { ROOT_PATH } from 'src/constants/resouce';
import useProductInfo from 'src/hooks/useProductInfo';
import useSurveysForBestFit from 'src/hooks/useSurveysForBestFit';
import { share } from 'src/utils/share';

const contentLayout = css`
  text-align: center;
  padding: 40px 20px;
`;

const productLayout = css`
  padding: 0px 28px;
`;

const productImageLayout = css`
  border-radius: 16px;
`;

const productDescriptionLayout = css`
  word-break: keep-all;
`;

const graySecondary = css`
  color: #666666;
`;

const shoppingBasketButton = css`
  padding: 3px 8px;
  line-height: 18px;
  border-radius: 4px;
`;

const black = css`
  color: #000000;
`;

const productTailLayout = (color: string) => css`
  padding: 0px 20px 40px 20px;
  background: ${color};
`;

const productButtonAlignment = (color: string) => css``;

const stampButtonAlignment = css`
  line-height: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
`;

const BestFitProductResultPaper = (): JSX.Element => {
  const router = useRouter();
  const { asPath, query } = router;
  const type = Number(query.type ?? 0);
  const id = Number(query.id ?? 0);

  const { name: brandName, readableName } = useProductInfo();
  const { customColorSet, results } = useSurveysForBestFit({
    id,
  });
  const result = results.at(type) ?? results[0];
  const { resultImageUrl, resultContentUrl, productId } = result;
  const { mainColor } = customColorSet;

  console.log('mainColor', mainColor);
  console.log('mainColor', mainColor);

  const title = `나에게 딱 맞는 ${readableName} 제품`;

  const handleClickShareThisPage = async () => {
    try {
      const sharedResult = await share({
        title,
        text: `${title} ${brandName} 를 공유합니다.`,
        url: `${ROOT_PATH}${asPath}`,
      });
      console.log('handleClickShareThisPage', sharedResult);
      window.alert(sharedResult);
    } catch (e) {
      if (e instanceof Error) {
        console.log(e.message as string);
      } else {
        console.log('error!!');
      }
    }
  };

  return (
    <Paper>
      <Head>
        <title>{title}</title>
        <meta property="og:title" content={title} />
        <meta
          property="og:description"
          content={`${title} ${brandName} 를 공유합니다.`}
        />
        <meta property="og:url" content={`${ROOT_PATH}${asPath}`} />
        <meta property="og:image" content={resultImageUrl} />
      </Head>
      <Stack fullWidth>
        <img alt="summary" width="100%" src={resultImageUrl} />
        <img alt="details" width="100%" src={resultContentUrl} />
        <Stack css={productTailLayout(mainColor)}>
          <Button
            variant="outlined"
            color={mainColor}
            height={48}
            css={productButtonAlignment(mainColor)}
          >
            제품 자세히 보기
          </Button>
        </Stack>
        <Stack justifyContent="space-between" css={contentLayout}>
          <Stack spacing={16}>
            <Typography variant="titleLarge">
              테스트 결과를 확인했다면
            </Typography>
            <Button color={mainColor} height={48} css={stampButtonAlignment}>
              스탬프 1개 받기
            </Button>
            <Typography variant="titleLarge">
              결과 공유하면 $주기$ 스탬프 1개
            </Typography>
            <Button
              variant="outlined"
              color={mainColor}
              height={48}
              css={stampButtonAlignment}
              onClick={handleClickShareThisPage}
            >
              결과 공유하고 스탬프 1개 받기
            </Button>
          </Stack>
        </Stack>
      </Stack>
    </Paper>
  );
};

export default BestFitProductResultPaper;
