import { css } from '@emotion/react';
import { useRouter } from 'next/router';

import { FullHeightBox, Stack } from 'src/components/layout';
import { LinkButton } from 'src/components/navigation';
import { Paper } from 'src/components/surface';
import { BASE_PATH, ROOT_PATH } from 'src/constants/resouce';
import useProductInfo from 'src/hooks/useProductInfo';
import useSurveysForBestFit from 'src/hooks/useSurveysForBestFit';

const contentLayout = css`
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 24px;
`;

const button = (color: string) => css`
  height: 48px;

  padding-top: 12px;
  line-height: 24px;
  padding-bottom: 12px;

  border: none;
  border-radius: 8px;

  color: #ffffff;
  background: ${color};

  width: 100%;
`;

const removeFirstSlash = (path: string) => {
  if (!path || !path.length) {
    return path;
  }
  const first = path.substring(0, 1);
  if (first !== '/') {
    return path;
  }
  return path.substring(1);
};

const BestFitProductIntroPaper = (): JSX.Element => {
  const router = useRouter();
  const { pathname = '', basePath = '' } = router;
  const { name: brandName, logoUrl } = useProductInfo();
  const { mainImageUrl, customColorSet } = useSurveysForBestFit({ id: 1 });
  const { mainColor } = customColorSet;

  const hrefForSurvey = `${ROOT_PATH}${BASE_PATH}${basePath}${removeFirstSlash(
    pathname,
  )}/survey?product_name=${brandName}`;

  return (
    <Paper backgroundUrl={mainImageUrl}>
      <FullHeightBox>
        <Stack
          spacing={20}
          justifyContent="flex-end"
          alignItems="center"
          fullWidth
          css={contentLayout}
        >
          <LinkButton css={button(mainColor)} href={hrefForSurvey}>
            테스트 시작하기
          </LinkButton>
        </Stack>
      </FullHeightBox>
    </Paper>
  );
};

export default BestFitProductIntroPaper;
