import { css } from '@emotion/react';

const style = {
  default: css`
    border-radius: 8px;
  `,
  height: (value: number) => css`
    height: ${value}px;
  `,
  fullWidth: css`
    width: 100%;
  `,
};

const WHITE = '#ffffff';
const DEFAULT_BACKGROUND = WHITE;
const CONTAINED = 'contained';
const OUTLINED = 'outlined';
const VARIANTS = [CONTAINED, OUTLINED];
type Variant = (typeof VARIANTS)[number];

const getVariantStyle = (
  variant: Variant,
  color: string,
  background: string = DEFAULT_BACKGROUND,
) => {
  switch (variant) {
    case OUTLINED:
      return css`
        color: ${color};
        background: ${background};
        border: solid 1px ${color};
      `;
    case CONTAINED:
    default:
      return css`
        color: ${background};
        background: ${color};
        border: none;
      `;
  }
};

interface ButtonProps {
  variant?: Variant;
  color: string;
  background?: string;
  height?: number;
  fullWidth?: boolean;
  onClick?: () => void;
  children?: React.ReactNode;
}
const Button = ({
  variant = 'contained',
  color,
  background = DEFAULT_BACKGROUND,
  height,
  fullWidth,
  onClick,
  children,
  ...others
}: ButtonProps): JSX.Element => {
  return (
    <button
      type="button"
      onClick={onClick}
      css={[
        style.default,
        getVariantStyle(variant, color, background),
        fullWidth && style.fullWidth,
        height && style.height(height),
      ]}
      {...others}
    >
      {children}
    </button>
  );
};

export default Button;
